const artistService = function($http, config) {
    
    const vm = this
    
    vm.getTopArtists = function() {
        return $http
            .get(config.apiUrl+"?method=chart.gettopartists&page=1&limit=5"+config.apiKey)
            .then(function(success){
                console.log(2)
                console.log(success)
            })
            .catch(function(e){
                console.log(e)
            })
    }

    
}

export default artistService



 




   